
import { defineComponent } from 'vue'
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import { PAGE_SIZE } from '@/shared/constants/common.const';
import { ICM21400111_REQ_BODY, ICM21400111_RES, BRANCH_LIST } from '@/shared/type/ICM21400111.type';
import DataformatService from "@/shared/services/DataFormatService";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

const dataFormatService = new DataformatService();

export default defineComponent({
      name: 'icm214001sc00',
      components: {
            Pagination,
            BreadCrumb,
            SelectOption
      },
      data() {
            const fieldSearch = {
                  select: '',
                  keyword: '',
                  displayYN: '',
                  statusCode: ''
            }
            const dataTable = [] as BRANCH_LIST[];
            return {
                  dataTable,
                  fieldSearch,
                  totalItems: 0,
                  currentPage: 1,
                  pageSize: PAGE_SIZE,
                  startingIndex: 1,
                  resetPage: false,
            }
      },

      created() {
            this.inquiryBranchList();
      },

      computed: {
            headers() {
                  return [
                        { columnName: "No." },
                        { columnName: "View" },
                        { columnName: "Branch Code" },
                        { columnName: "Branch Name" },
                        { columnName: "Province Code" },
                        { columnName: "Address" },
                        { columnName: "Telephone" },
                        { columnName: "Status" },
                        { columnName: "Created date" },
                        { columnName: "Display" },
                  ]
            },

            displayYN() {
                  return this.$store.state.CommonCodeModule.commonCode.YnType
            },
      },

      methods: {
            async inquiryBranchList(mode?: string) {
                  const reqBody: ICM21400111_REQ_BODY = {
                        displayYN: this.fieldSearch.displayYN,
                        searchValue: this.fieldSearch.keyword,
                        pageNumber: mode == 'search' ? 0 : this.currentPage,
                        pageSize: this.pageSize,
                        statusCode: this.fieldSearch.statusCode
                  }

                  if (mode === 'search') {
                        this.currentPage = 1
                        this.resetPage = true;
                  }

                  const res = await this.$request.getRequest('ICM21400111', reqBody, false) as ICM21400111_RES;

                  if (res.header.result) {
                        this.dataTable = JSON.parse(JSON.stringify(this.mapDateTable(res.body.list)));
                        this.totalItems = res.body.totalCount;
                  }
            },

            mapDateTable(data: BRANCH_LIST[]): BRANCH_LIST[] {
                  return data.map((item: BRANCH_LIST) => {
                        return {
                              branchName: item.branchName,
                              branchAddress: item.branchAddress,
                              branchStatus: `${item.statusCode} : ${this.$codeConverter.convertValue("ContentManangementStatusCode", item.statusCode)}`,
                              branchTel: dataFormatService.formatPhoneNumber(item.branchTel),
                              branchCode: item.branchCode,
                              provinceCode: `${item.provinceCode ? item.provinceCode + ' : ' : ''} ${this.$codeConverter.convertValue("ProvinceCode", item.provinceCode)}`,
                              firstRegisterDate: this.$datetime.formatVueDatePicker(this.$datetime.convertStringToDate(`${item.firstRegisterDate}`)),
                              displayYN: item.displayYN === "Y" ? "Yes" : "No",
                              statusCode: item.statusCode
                        }
                  })
            },

            handleRefresh() {
                  this.resetPage = true;
                  this.currentPage = 1;
                  this.fieldSearch.statusCode = ''
                  this.fieldSearch.displayYN = '';
                  this.fieldSearch.keyword = '';

                  this.inquiryBranchList();

            },

            handleRegister() {
                  this.$router.push({
                        path: '/ICM214001SC01',
                        query: {
                              type: "register"
                        }
                  });
            },

            handleRowClick(rowData: BRANCH_LIST) {
                  this.$router.push({
                        path: 'ICM214001SC01',
                        query: {
                              type: "edit",
                              branchCode: rowData.branchCode,
                        }
                  });
            },

            handlePagination(pageNumber: number) {
                  this.currentPage = pageNumber
                  this.resetPage = false;
                  this.inquiryBranchList();
            },

            handleStartingIndex(startingIndex: number) {
                  this.startingIndex = startingIndex;
            },

      }
})
